import { useState } from 'react';
import { APP_API_URL, COMPANY_NAME, IS_TAGEMBED } from '../../../../../actions/api';
import { Image, Button, Container } from 'react-bootstrap';
import EmptyWithIcon from '../../CommonComponents/Empty/EmptyWithIcon';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { useNavigate } from 'react-router-dom';
import { COPY_TO_CLIPBOARD, openCenteredWindow, showSalesModal } from '../../../../../utils';

export default function Api({ userData, updateSalesModal }) {
  const [copied, setCopied] = useState(false);
  const wallKey = userData?.user?.user_key ?? "";
  const navigate = useNavigate()

  const getRedirectionUrl = (type) => {
    let apiUrl = `${APP_API_URL}api/v2/widget/posts/${localStorage.getItem('wallId')}?user_key=${wallKey}&format=${type}`;
    if (IS_TAGEMBED) apiUrl = 'https://tagembed.com/support/api-documentation/';
    openCenteredWindow(apiUrl);
  }

  const copyText = (id) => {
    COPY_TO_CLIPBOARD(id)
    setCopied(true)
    setTimeout(setCopied, 1000)
  }

  return <Container fluid>
    {userData?.UserRule?.api ?
      <div>
        {IS_TAGEMBED ? <div className="input-icon input-icon-right mb-4 position-relative d-flex align-items-center mt-2 w-100">
          <label className="mb-0 me-2" style={{ whiteSpace: 'nowrap' }}>User Key</label>
          <div className="position-relative d-flex align-items-center w-100">
            <input onClick={() => copyText("copy_url-api")} id={"copy_url-api"} type="text" className="form-control overflow-hidden text-ellipsis text-nowrap me-2 edit_url font-weight-bolder" placeholder="" value={wallKey} readOnly />
            {copied ? <span id="copy-success-alert" style={{ display: 'block' }}>Copied!</span> : null}
            <span className="w-auto">
              <button style={{ width: '39px', height: '39px' }} onClick={() => copyText("copy_url-api")} className="btn btn-icon icon-xs copy-btn-display" data-factors-click-bind="true">
                <FontAwesomeIcon type="solid" icon={'copy'} />
              </button>
            </span>
          </div>
        </div> : null}
        <div className="f-center py-10 custom-api-section">
          <Button variant='light' className='mx-2 f-center' onClick={() => getRedirectionUrl('json')}>
            <Image src={baseUrl('media/images/json.png')} height={70} width={70} fluid />
          </Button>
          <Button variant='light' className='mx-2 f-center' onClick={() => getRedirectionUrl('xml')}>
            <Image src={baseUrl('media/images/xml.png')} height={70} width={70} fluid />
          </Button>
          {IS_TAGEMBED ? null : <Button variant='light' className='mx-2 f-center' onClick={() => getRedirectionUrl('rss')} style={{ height: 200, width: 200 }}>
            <Image src={baseUrl('media/images/rss.png')} height={70} width={70} fluid />
          </Button>}
        </div>
      </div>
      :
      <div className='f-center' style={{ minHeight: 'calc(100vh - 300px)' }}>
        <EmptyWithIcon title="API Access" desc={`Get API access with our upgraded plan to integrate ${COMPANY_NAME} gallery in your application and make your app stand out.`} error={'api-access-error'} button={{ action: () => updateSalesModal(showSalesModal(userData)), text: 'Talk to Sales' }} isFullScreen={false} />
      </div>
    }
  </Container>
}
